/**
 */
export interface Toc extends BaseAreaView<IRegPointer<IWspUiEnv>> {
	initialize(init: OTocInit): this
}

export interface OTocInit extends OBaseAreaViewInit<IRegPointer<IWspUiEnv>>,
	PickInit<Toc, 'infoBroker' | 'transformFacet' | 'tocItemTpl'> {
	/** Masque l'entÃªte avec le ptrItem. */
	hideHeader?: boolean

	/** nombre maximal de niveaux Ã  dÃ©plier Ã  l'init */
	maxLevelOpenedIntended?: number

	lastDatas?: JLDToc
}

interface JLDToc {
	roots: srcRef[]
}

export class Toc extends BaseAreaView<IRegPointer<IWspUiEnv>> implements IInfoConsumer, ILastDatasBuilder {
	reg: IReg<IWspUiEnv>;
	wsp: Wsp;

	/** RÃ©agira aux infos issues de ce broker (item courant) et dispatchera les demandes de focus / sÃ©lection. */
	infoBroker: IInfoBroker;

	/**
	 * ParamÃ¨tres de la facette de transformation pour obtenir la toc
	 * Valeur par dÃ©faut : "facet=outline"
	 * IntÃ©grÃ© ainsi dans la requÃªte: `transform=facet&${transformFacet}`
	 */
	transformFacet?: string;

	/** Personnalisation de la construction de la TOC. */
	tocItemTpl: (this: Toc, tocItem: ITocItem, hideTitle: boolean, depthTitle: number, stackItems: ITocItem[]) => TemplateResult;

	/* Map des Toc des items impliquÃ©s. Double indexation srcUri et srcId. */
	protected _srcMap: Map<srcUri & srcId, ITocItem>;

	protected _srcHisto?: SrcHisto;

	_rootSrc: SrcPointer;
	protected _view: HTMLElement;


	/** SrcUri actuellement "courrant" (InfoCurrentItem) pour le repÃ©rer dans la TOC. */
	protected _currentSrcUri: srcUri;

	setRootSrcRef(srcRef: srcRef) {
		this._rootSrc.setSrcRef(srcRef);
	}

	getRootSrcRef(): srcRef {
		return this._rootSrc.srcRef;
	}

	getRootSrcUri(): srcUri {
		const sd = this._rootSrc.shortDescs[0];
		return sd ? sd.srcUri : null;
	}

	/** Retourne true si le noeud de l'arbre doit Ãªtre ouvert */
	protected isLevelOpened(tocItem: ITocItem, depth: Number): boolean {
		return depth < this._maxLevelOpenedIntended;
	}

	protected _maxLevelOpenedIntended: number = 5

	isInToc(srcRef: srcRef): boolean {
		if (!this._srcMap) return false;
		return this._srcMap.has(srcRef);
	}

	async ensureRowVisible(srcUri: srcUri) {
		//TODO ensureRowVisible
	}

	protected _initialize(init: OTocInit) {
		super._initialize(init); //init this.reg, this.area, this.areaContext
		this.wsp = this.reg.env.wsp;
		this.infoBroker = init.infoBroker;
		this.transformFacet = init.transformFacet ? `transform=facet&${init.transformFacet}` : "transform=facet&facet=outline";

		if (init.maxLevelOpenedIntended) this._maxLevelOpenedIntended = init.maxLevelOpenedIntended;

		//Construction Ui
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(this.localName, init);
		this.reg.installSkin('scroll/small', sr);

		this._rootSrc = new SrcPointer().initialize({
			reg: this.reg,
			place: this.reg.env.place,
			tpl: SRCDRAWER.itemCodeAloneTpl,
			defaultAction: this.reg.getSvc("ptrItemDblClick", SrcPointerDblClick.SINGLETON),
			sgnPattern: /.*#Map\b.*/,
			draggable: true,
			dropable: true,
			dispatchHighlight: true,
		});
		this._rootSrc.classList.add("fixedIconSize");

		if (!init.hideHeader) {
			//const ld = init.lastDatas?[this.getAttribute('last-datas')];
			this._srcHisto = <SrcHisto title="Historique des derniÃ¨res racines sÃ©lectionnÃ©es" Ã®={{
				reg: this.reg,
				icon: "/@skin@/wsp/views/toc/histo.svg",
				srcPointer: this._rootSrc,
				history: init.lastDatas?.roots
			} as OSrcHistoInit}/> as SrcHisto;
			sr.appendChild(<div id="headBar">
				<div id="rootSel">
					{this._rootSrc}
					{this._srcHisto}
				</div>
				<BarActions id="options" Ã®={{
					actions: [new Action<Toc>()
						.setLabel("DÃ©plier d'un niveau supplÃ©mentaire")
						.setIcon("/@skin@/commons/icons/expandMore.svg")
						.setExecute((ctx) => {
							ctx._maxLevelOpenedIntended++;
							ctx._render();
						}),
						new Action<Toc>()
							.setLabel("Tout replier")
							.setIcon("/@skin@/commons/icons/collaps.svg").setExecute((ctx) => {
							ctx._maxLevelOpenedIntended = 1;
							ctx._render();
						})],
					disableFullOverlay: true,
					uiContext: "bar",
					actionContext: this
				} as OBarActionsInit<Toc>}/>
			</div>);
		}

		this._rootSrc.onSrcRefChange.add(() => {this.rebuildFromRoot()});

		this._view = sr.appendChild(<div id="view"/>);

		//Template de la toc
		this.tocItemTpl = init.tocItemTpl || function (this: Toc, tocItem: ITocItem, hideTitle: boolean, depthTitle: number, stackItems: ITocItem[]): TemplateResult {
			//item en erreur
			if (tocItem.srcSt < 0) return xhtml`<div class="notFound"><wsp-toc-twisty p.state="${'notFound'}"/>Item non trouvÃ©</div>`;
			//check cycles
			if (stackItems.indexOf(tocItem) >= 0) {
				return xhtml`<wsp-toc-title depth="${depthTitle}"><wsp-toc-twisty p.state="${'cycle'}" srcRef="${tocItem.srcId || tocItem.srcUri}"/>${tocItem.title || SRC.extractLeafFromUri(tocItem.srcUri)}</wsp-toc-title>`;
			}
			stackItems.push(tocItem);
			//Tpl des nodes
			const tocNodeTpl = (tocNode: ITocNode): TemplateResult => {
				try {
					depthTitle++;
					const defaultState = this.isLevelOpened(tocItem, depthTitle) ? "opened" : "closed";
					if (typeof tocNode.body === 'string') {
						const subTocItem = this._srcMap.get(tocNode.body);
						if (subTocItem) {
							return xhtml`<wsp-toc-node p.tocNode="${tocNode}">
							${tocNode.title ? xhtml`<wsp-toc-title depth="${depthTitle}"><wsp-toc-twisty p.state="${subTocItem.nodes.length ? defaultState : 'leaf'}"/>${tocNode.title || tocNode.name}</wsp-toc-title>` : undefined}
							${this.tocItemTpl(subTocItem, tocNode.title != null, depthTitle, stackItems)}
						</wsp-toc-node>`
						} else {
							//TODO tocItem pas encore chargÃ© ou chargement en echec ?
							return xhtml`<div>...</div>`;
						}
					} else {
						return xhtml`<wsp-toc-node p.tocNode="${tocNode}">
						<wsp-toc-title depth="${depthTitle}"><wsp-toc-twisty p.state="${tocNode.body && tocNode.body.length ? defaultState : 'leaf'}"/>${tocNode.title || tocNode.name}</wsp-toc-title>
						<div class="children">
							${tocNode.body ? tocNode.body.map(tocNodeTpl) : undefined}
						</div>
					</wsp-toc-node>`
					}
				} finally {
					depthTitle--;
				}
			};

			return xhtml`<wsp-toc-item p.tocItem="${tocItem}" srcRef="${tocItem.srcId || tocItem.srcUri}">
				<wsp-toc-side b.current="${this._currentSrcUri === tocItem.srcUri}"/>
				<div class="itemBody">
					${hideTitle ? undefined : xhtml`<wsp-toc-title depth="${depthTitle}"><wsp-toc-twisty p.state="${tocItem.nodes.length ? (this.isLevelOpened(tocItem, depthTitle) ? "opened" : "closed") : 'leaf'}"/>${tocItem.title || SRC.extractLeafFromUri(tocItem.srcUri)}</wsp-toc-title>`}
					<div class="children">
						${tocItem.nodes.map(tocNodeTpl)}
					</div>
				</div>
			</wsp-toc-item>`;
		}
	}

	/** AppeplÃ© Ã  l'init, au chgt de src root et en cas de perte de connexion. */
	protected async rebuildFromRoot() {
		this._srcMap = null;
		this._fetchToc(this._rootSrc.srcRef);
	}

	protected async _fetchToc(srcRef: srcRef) {
		if (srcRef) {
			try {
				const txt = await WSP.fetchStreamText(this.wsp, this, srcRef, this._srcMap ? this.transformFacet + "&depth=1" : this.transformFacet);
				//console.log("_fetchToc::::", txt);
				const doc = DOM.parseDom(txt);
				if (!this._srcMap) this._srcMap = new Map();
				this.buildItemToc(doc.documentElement.firstElementChild);
			} catch (e) {
				this._srcMap = null;
				console.log(e);
			}
		} else {
			this._srcMap = null;
		}
		this._render();
	}

	/** Analyse un element DOM "item" pour enrichir this._srcMap. */
	protected buildItemToc(item: Element) {
		const srcUri = item.getAttribute("uri");
		const srcId = item.getAttribute("id");

		//cleanup
		let old = this._srcMap.get(srcUri);
		if (old && old.srcId && old.srcId !== srcId) this._srcMap.delete(old.srcId);
		old = this._srcMap.get(srcId);
		if (old && old.srcUri !== srcUri) this._srcMap.delete(old.srcUri);

		const root = DOM.findFirstChild(item, (n: Node): n is Element => n.nodeName === 'itemRoot');
		if (!root) return; //pas de <itemRoot> == prÃ©sence d'un <cycle/>, on sort.
		const title = DOM.findFirstChild(root, (n: Node): n is Element => n.nodeName === 'titleNode');
		const tocItem = {
			srcUri: srcUri,
			srcId: srcId,
			srcSt: parseInt(item.getAttribute("st")),
			itSgn: item.getAttribute("sgn"),
			xpath: root ? root.getAttribute("xp").split('/') : null,
			title: title ? title.getAttribute("title") : null,
			nodes: []
		} as ITocItem;
		this._srcMap.set(srcUri, tocItem);
		if (srcId) this._srcMap.set(srcId, tocItem);
		const children = DOM.findFirstChild(root, (n: Node): n is Element => n.nodeName === 'children');
		if (!children) return;
		for (let ch = children.firstElementChild; ch; ch = ch.nextElementSibling) {
			if (ch.nodeName === "node") tocItem.nodes.push(this.buildNodeToc(ch));
		}
	}

	/** Analyse un element DOM nommÃ© "node" et retourne un ITocNode. */
	protected buildNodeToc(node: Element): ITocNode {
		const title = DOM.findFirstChild(node, (n: Node): n is Element => n.nodeName === 'titleNode');
		const itNode = {
			xpath: node.getAttribute("xp").split('/'),
			name: node.getAttribute("name"),
			title: title ? title.getAttribute("title") : null
		} as ITocNode;
		const body = node.lastElementChild;
		if (body) {
			if (body.nodeName === "children") {
				const bodySet: ITocNode[] = itNode.body = [];
				for (let ch = body.firstElementChild; ch; ch = ch.nextElementSibling) {
					if (ch.nodeName === "node") bodySet.push(this.buildNodeToc(ch));
				}
			} else if (body.nodeName === "item") {
				const srcRef: srcRef = itNode.body = body.getAttribute("id") || body.getAttribute("uri");
				if (body.hasChildNodes() || body.getAttribute("st") === "-1") {
					//dÃ©finition de cet item dans le flux.
					this.buildItemToc(body);
				} else {
					//Non prÃ©sent dans le flux XML fetchÃ©
					if (!this._srcMap.has(srcRef)) {
						//srcUri non prÃ©sente dans la map de cette toc, on doit charger la toc de cet item
						this._fetchToc(srcRef);
					}
				}
			}
		}
		return itNode;
	}

	_render() {
		const rootSrcUri = this.getRootSrcUri();
		if (!rootSrcUri) {
			render(this.noRootTpl(), this._view);
			return;
		}
		const tocItem = this._srcMap?.get(rootSrcUri);
		if (!tocItem) {
			render(this.noTocTpl(), this._view);
			return;
		}
		render(this.tocItemTpl(tocItem, false, 1, []), this._view);
	}


	noRootTpl(): TemplateResult {
		return xhtml`<c-msg level="info">Renseignez un item racine</c-msg>`;
	}

	noTocTpl(): TemplateResult {
		return xhtml`<c-msg level="info">Aucun plan pour cet item</c-msg>`;
	}

	/** Identifie l'item courant dans l'app. */
	_setCurrentItem(srcUri: srcUri | null) {
		this._currentSrcUri = srcUri;
		this._render();
		if (this._currentSrcUri) this.ensureRowVisible(this._currentSrcUri);
	}

	buildLastDatas?(parentLastDatas: JLastDatas): void {
		const ldKey = this.getAttribute('last-datas');
		if (ldKey) parentLastDatas[ldKey] = this._srcHisto?.setHistory<JLDToc>({} as JLDToc, "roots");
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoCurrentItem) {
			this._setCurrentItem(info.srcUri);
		}
	}

	onViewShown() {
		if (!this._onConnRenewed) {
			//1er init
			this._onConnRenewed = this.onConnRenewed.bind(this);
			this.reg.env.place.eventsMgr.on("onConnectionRenewed", this._onConnRenewed);
			this._wspUriChange = this.onWspUriChange.bind(this);
			this.reg.env.place.eventsMgr.on("wspUriChange", this._wspUriChange);
		}
		if (this.infoBroker) {
			this.infoBroker.addConsumer(this);
			const req = new InfoReqCurrentItem();
			this.infoBroker.dispatchInfo(req, this);
			this._setCurrentItem(req.srcUri);
			if (this._srcMap == null) {
				if (!this._rootSrc.srcRef && req.srcUri) {
					//On prend l'item courant par dÃ©faut.
					this._rootSrc.setSrcRef(req.srcUri, req.shortDesc);
				}
			}
		}
	}

	onViewHidden(closed?: boolean): void {
		if (this.infoBroker) this.infoBroker.removeConsumer(this);
		if (closed) {
			this.reg.env.place.eventsMgr.removeListener("onConnectionRenewed", this._onConnRenewed);
			this.reg.env.place.eventsMgr.removeListener("wspUriChange", this._wspUriChange);
		}
	}

	protected _wspUriChange: (msg: JWspUriChangeMsg, from: WspsLivePlace | 'local' | 'server') => void;

	protected onWspUriChange(msg: JWspUriChangeMsg, from: WspsLivePlace | 'local' | 'server') {
		if (!this._srcMap) return;
		if (msg.type === EWspChangesEvts.u) {
			const tocIt = this._srcMap.get(SRC.srcRef(msg));
			if (tocIt) this._fetchToc(SRC.srcRef(msg));
		} else if (msg.type === EWspChangesEvts.r) {
			const tocIt = this._srcMap.get(msg.srcUri);
			if (tocIt) {
				tocIt.srcSt = -1;
				tocIt.nodes = [];
				this._render();
			}
		}
	}

	protected _onConnRenewed: () => void;

	protected async onConnRenewed() {
		if (this.hidden) {
			if (this._srcMap) {
				//clear la view
				this._srcMap = null;
				this._render();
			}
		} else {
			this.rebuildFromRoot();
		}
	}
}

