customElements.define("wsp-toc", Toc);

interface ITocItem {
	srcUri: srcUri
	srcId: srcId
	itSgn: string
	srcSt: number

	/**
	 * Title associÃ© Ã  la racine de l'item. Soit c'est le titre de la racine de la TOC, soit
	 * ce sera le titre des ITocNode dans les quels cet item est rÃ©fÃ©rencÃ© (part/@sc:refUri avec des metas Ã  la part).
	 */
	title?: string

	/** Fragments du xpath pointant le noeud parent des ITocNode fils. */
	xpath: string[]

	nodes: ITocNode[]
}

interface ITocNode {

	/** XPath relatif Ã  son contexte parent. */
	xpath: string[]

	/** Nom du modÃ¨le du node : utilisÃ© si pas de title.*/
	name: string

	/**
	 * Titre du noeud. Si le contenu de ce noeud est externalisÃ© (part/@sc:refUri), et que ce titre est renseignÃ©,
	 * il s'agit du titre issu des metas de la part qui doit donc surchargÃ© le titre du ITocItem pointÃ©.
	 */
	title?: string

	/** Soit tout le body du node est portÃ© par un autre item (part avec un ptrItem), soit il contient sa liste de fils. */
	body?: srcUri | ITocNode[]
}


export class WspTocItem extends HTMLElement {
	tocItem: ITocItem;

	buildXpath(xpath: string[]): srcRef {
		if (this.tocItem.xpath) xpath.push(...this.tocItem.xpath);
		return this.getAttribute("srcRef");
	}
}

customElements.define("wsp-toc-item", WspTocItem);


export class WspTocNode extends HTMLElement {
	tocNode: ITocNode;

	buildXpath(xpath: string[]): srcRef {
		const p = this.parentElement.closest("wsp-toc-node,wsp-toc-item") as WspTocNode | WspTocItem;
		const srcRef = p.buildXpath(xpath);
		if (this.tocNode.xpath) xpath.push(...this.tocNode.xpath);
		return srcRef;
	}
}

customElements.define("wsp-toc-node", WspTocNode);


export class WspTocTitle extends HTMLElement {
	constructor() {
		super();
		BASIS.makeClickable(this);
		this.onclick = this.onClick;
		this.onkeydown = this.onKeyDown;
	}

	onClick(ev: MouseEvent) {
		const p = this.parentElement.closest("wsp-toc-node,wsp-toc-item") as WspTocNode | WspTocItem;
		const xp = [] as string[];
		const srcRef = p.buildXpath(xp);
		const toc = DOMSH.findHost<Toc>(this);
		toc.infoBroker.dispatchInfo(new InfoFocusNodeInItem(xp.join("/"), srcRef), toc);
	}

	openFolder(): boolean {
		const twisty = this.querySelector("wsp-toc-twisty") as WspTocTwisty;
		if (twisty && twisty.state === "closed") {
			twisty.state = "opened";
			return true;
		}
		return false;
	}

	closeFolder(): boolean {
		const twisty = this.querySelector("wsp-toc-twisty") as WspTocTwisty;
		if (twisty && twisty.state === "opened") {
			twisty.state = "closed";
			return true;
		}
		return false;
	}

	toggleFolderOrGotoCycle(): boolean {
		const twisty = this.querySelector("wsp-toc-twisty") as WspTocTwisty;
		if (twisty) {
			const st = twisty.state;
			if (st === "closed") {
				twisty.state = "opened";
				return true;
			} else if (st === "opened") {
				twisty.state = "closed";
				return true;
			} else if (st === "cycle") {
				twisty.gotoCycle();
				return true;
			}
		}
		return false;
	}

	ensureVisible() {
		this.scrollIntoView({block: 'nearest', inline: 'nearest', behavior: 'smooth'});
	}

	onKeyDown(ev: KeyboardEvent) {
		let n;
		switch (ev.key) {
		case 'ArrowDown' :
			n = DOM.findNext(this, null, IS_WspTocTitle);
			if (n) n.focus();
			break;
		case 'ArrowUp' :
			n = DOM.findPrevious(this, null, IS_WspTocTitle);
			if (n) n.focus();
			break;
		case 'ArrowLeft' :
			if (window.getComputedStyle(this).direction === 'rtl') {
				if (!this.openFolder()) {
					this.dispatchEvent(new KeyboardEvent('keydown', {key: 'ArrowDown'}));
					return;
				}
			} else {
				if (!this.closeFolder()) {
					this.dispatchEvent(new KeyboardEvent('keydown', {key: 'ArrowUp'}));
					return;
				}
			}
			this.ensureVisible();
			break;
		case 'ArrowRight' :
			if (window.getComputedStyle(this).direction === 'rtl') {
				if (!this.closeFolder()) {
					this.dispatchEvent(new KeyboardEvent('keydown', {key: 'ArrowUp'}));
					return;
				}
			} else {
				if (!this.openFolder()) {
					this.dispatchEvent(new KeyboardEvent('keydown', {key: 'ArrowDown'}));
					return;
				}
			}
			this.ensureVisible();
			break;
		case 'Enter' :
			if (ACTION.isAccelPressed(ev)) {
				if (!this.toggleFolderOrGotoCycle()) return;
				this.ensureVisible();
				break;
			}
		default :
			//	console.log("evt keydown:::"+evt.keyCode);
			return;
		}
		ev.stopImmediatePropagation();
		ev.preventDefault();
	}

}

function IS_WspTocTitle(n: Node): n is WspTocTitle {return n instanceof WspTocTitle}

customElements.define("wsp-toc-title", WspTocTitle);

export class WspTocTwisty extends HTMLElement {
	constructor() {
		super();
		this.onpointerdown = this.onPointerDown;
		this.onclick = this.onClick;
	}

	get state(): 'leaf' | 'opened' | 'closed' | 'notFound' | 'cycle' {return this.getAttribute('state') as any}

	set state(st: 'leaf' | 'opened' | 'closed' | 'notFound' | 'cycle') {
		switch (st) {
		case 'opened': {
			const chList = this.getChildrenList();
			if (chList) DOM.setHidden(chList, false);
			break;
		}
		case 'closed': {
			const chList = this.getChildrenList();
			if (chList) DOM.setHidden(chList, true);
			break;
		}
		case 'leaf' :
		case 'notFound':
		case 'cycle': {
			break;
		}
		default:
			throw Error("State unknown: " + st);
		}
		this.setAttribute('state', st);
	}

	gotoCycle(): boolean {
		if (this.state === "cycle") {
			const parent = DOM.findParent(this, null, (n): n is HTMLElement => n instanceof WspTocItem && n.getAttribute('srcRef') === this.getAttribute('srcRef'));
			if (parent) {
				const title = parent.querySelector<HTMLElement>('wsp-toc-title');
				if (title) {
					title.focus();
					return true;
				}
			}
		}
		return false;
	}

	getChildrenList(): HTMLElement {
		const parent = DOM.findParent(this, null, (n): n is HTMLElement => n instanceof WspTocNode || (n instanceof HTMLElement && n.classList.contains("itemBody")));
		return DOM.findLastChild(parent, (n): n is HTMLElement => n instanceof HTMLElement && n.classList.contains("children"));
	}

	onPointerDown(ev: PointerEvent) {
		const st = this.state;
		if (st === "leaf") return;
		if (st === "cycle") {
			if (this.gotoCycle()) {
				ev.stopImmediatePropagation();
				ev.preventDefault(); //bloque la prise de focus sur notre propre wsp-toc-title.
			}
			return;
		}
		ev.stopImmediatePropagation();
		this.state = st === "closed" ? "opened" : "closed";
	}

	onClick(ev: PointerEvent) {
		ev.stopImmediatePropagation(); //bloque le gotoFocus dans les wed.
	}
}

customElements.define("wsp-toc-twisty", WspTocTwisty);

/**
 * Barre latÃ©rale reprÃ©sentant un item.
 * Note accessibilitÃ© : pour une naviegation standard dans l'arbre des titres du plan, on a choisit de ne pas rendre focusbale les WspTocSide.
 */
export class WspTocSide extends HTMLElement {
	constructor() {
		super();
		this.onclick = this.onClick;
	}

	onClick(ev: MouseEvent) {
		const toc = DOMSH.findHost<Toc>(this);
		toc.infoBroker.dispatchInfo(new InfoFocusItem(this.parentElement.getAttribute("srcRef")), toc);
	}
}

customElements.define("wsp-toc-side", WspTocSide);
